.benefits {
    padding-top: 40px;
    background-image: linear-gradient(
    to bottom,
    var(--main-color) 473px,
    var(--white-color) 473px);
    @media screen and (min-width:768px) {
        padding-top: 80px;
        background-image: linear-gradient(
            to bottom,
            var(--main-color) 264px,
            var(--white-color) 264px);
    }
    @media screen and (min-width:1680px) {
        padding-top: 237px;
        background-image: linear-gradient(
            to bottom,
            var(--main-color) 436px,
            var(--white-color) 436px);
    }
    &__container {
        @extend %container;
    }
    &__title {
        @extend %font-title;
        color: var(--white-color);
        padding-bottom: 32px;
        @media screen and (max-width:767px) {
            max-width:180px;
        }
        @media screen and (min-width:768px) {
        text-align: center;
        padding-bottom: 40px;
        }
        @media screen and (min-width:1680px) {
            // @extend %visually-hidden
            position: absolute;
            width: 1px;
            height: 1px;
            margin: -1px;
            border: 0;
            padding: 0;
            white-space: nowrap;
            clip-path: inset(100%);
            clip: rect(0 0 0 0);
            overflow: hidden;
        }   
    }
    &__list {
        background-color: var(--white-color);
        box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
        padding: 20px 24px 20px 20px;
        @media screen and (min-width:768px) {
            display: flex;
            flex-wrap: wrap;
            padding: 40px 112px 40px 92px;
        }
        @media screen and (min-width:1680px) {
            padding: 60px 32px 60px 140px;
        }
    }
    &__item {
        @media screen and (max-width:767px) {
        &:not(:last-child) {
            margin-bottom: 40px;
            }
        }
        @media screen and (min-width:768px) and (max-width:1679px) {
            width: 236px;
            margin-right: 32px;
            margin-bottom: 44px;
            &:nth-child(2n) {
                width: 216px;
                margin-right: 0;
            }
            &:nth-last-child(-n + 2) {
                margin-bottom: 0;
            }
        }
        @media screen and (min-width:1680px) {
            width: 356px;
            margin-bottom: 72px;
            margin-right: 204px;
            &:nth-last-child(-n + 3) {
                margin-bottom: 0;
            }
            &:nth-child(3n) {
                margin-right: 0;
            }
        }
    }
    &__number {
        position: relative;
        margin-bottom: 8px;
        margin-left: 16px;
        color: var(--accent-color);
        // @include font(700, 14, 1.65, 0.08em);
        font-weight: 700;
        font-size: 14px;
        line-height: 1.65;
        letter-spacing: 0.08em;
        &::before {
            position: absolute;
            content: '';
            top: 11px;
            left: -16px;
            width: 12px;
            height: 1px;
            background-color: var(--accent-color);
          }
        
    }
    &__text {
        // @include font(700, 14, 1.43, 0.08em);
        font-weight: 700;
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: 0.08em;
        color: var(--main-color);
        @media screen and (min-width:1680px) {
            font-size: 20px;
            line-height: 1.4;
        }
    }
}